.Spinner {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
    left: 50%;
    text-align: center;
  }
  .Spinner div {
    position: absolute;
    border: 4px solid rgb(7, 7, 7);
    opacity: 1;
    border-radius: 50%;
    animation: Spinner 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  }
  .Spinner div:nth-child(2) {
    animation-delay: -0.5s;
  }
  @keyframes Spinner {
    0% {
      top: 28px;
      left: 28px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: -1px;
      left: -1px;
      width: 58px;
      height: 58px;
      opacity: 0;
    }
  }
  